export const query = graphql`
	query siteMeta {
		__typename
		allSite {
			edges {
				node {
					siteMetadata {
						title
						description
						name
						bigName
						intro
						dateModified
					}
				}
			}
		}
		allFile(
			filter: {
				sourceInstanceName: { eq: "basePages" }
				fields: { slug: { ne: null } }
			}
		) {
			nodes {
				relativePath
				base
				name
				sourceInstanceName
				internal {
					type
					contentDigest
					description
					mediaType
				}
				fields {
					slug
				}
				childMarkdownRemark {
					id
					html
					frontmatter {
						title
					}
				}
			}
			totalCount
		}
		allClip {
			totalCount
		}
		allAchievement(
			filter: {
				achievement: {
					type: { eq: "Education" }
					affiliation: { regex: "/Coll|Uni/" }
					title: { regex: "/Degree|Master|Bachelor/" }
				}
			}
		) {
			edges {
				node {
					id
					achievement {
						title
						slug
					}
				}
			}
		}
	}
`
